@import url('https://fonts.googleapis.com/css2?family=Creepster&display=swap');

html, body {
  margin: 0;
  padding: 0;
  height: 100%;
}

html {
  background: radial-gradient(circle at top, #2b0033, #000);
  background-attachment: fixed;
  background-size: cover;
}

body {
  background: radial-gradient(circle at top, #2b0033, #000);
  background-attachment: fixed;
  background-size: cover;
  min-height: 100vh;
  min-height: 100dvh; /* Dynamic viewport height for mobile */
  color: #fff;
  font-family: 'Creepster', cursive;
  text-align: center;
  overflow-x: hidden;
  
  /* Prevent mobile bounce scrolling white background */
  overscroll-behavior: none;
}

.App {
  padding-bottom: 2rem;
  min-height: 100vh;
  min-height: 100dvh; /* Dynamic viewport height for mobile */
  background: radial-gradient(circle at top, #2b0033, #000);
  background-attachment: fixed;
  background-size: cover;
}

.App h1 {
  font-size: 2.5rem;
  color: orange;
  text-shadow: 0 0 10px red;
  margin-top: 1rem;
}

.cards {
  display: flex;
  justify-content: center;
  gap: 2rem;
  flex-wrap: wrap;
  margin: 2rem auto;
}

.card {
  background: rgba(255, 255, 255, 0.1);
  padding: 1rem 2rem;
  border-radius: 1rem;
  box-shadow: 0 0 1.563rem orange;
  width: 16.25rem;
  transition: transform 0.3s ease;
  animation: subtleFloat 6s ease-in-out infinite;
}

.card:nth-child(odd) {
  transform: rotate(-1deg);
  animation-delay: -2s;
}

.card:nth-child(even) {
  transform: rotate(1deg);
  animation-delay: -4s;
}

.card:hover { 
  transform: scale(1.05) rotate(0deg);
  animation-play-state: paused;
}

.card-image {
  width: 100%;
  height: auto;
  border-radius: 1rem;
  margin-bottom: 0.5rem;
  box-shadow: 0 0 0.625rem orange;
  transition: transform 0.3s ease;
  animation: gentleSway 8s ease-in-out infinite;
}

.card-image:hover {
  transform: rotate(-2deg) scale(1.02);
}

button {
  background: #ffd700 !important; /* Force yellow with !important */
  border: none;
  color: black;
  font-weight: bold;
  margin: 0.3rem;
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  cursor: pointer;
  font-family: inherit;
  font-size: 1rem;
  transition: transform 0.2s ease;
  animation: subtleWobble 12s ease-in-out infinite;
}

button:nth-child(2n) {
  animation-delay: -3s;
}

button:nth-child(3n) {
  animation-delay: -6s;
}

button:hover {
  transform: rotate(-1deg) scale(1.05);
  animation-play-state: paused;
}

/* Override ALL possible button states to stay yellow */
button:hover,
button:focus, 
button:active,
button:visited,
button:focus-visible,
button:focus-within,
button:-webkit-any-link,
button:-moz-any-link {
  background: #ffd700 !important;
  background-color: #ffd700 !important;
  outline: none !important;
}

/* Mobile-specific overrides */
@media (hover: none) and (pointer: coarse) {
  button,
  button:hover,
  button:focus,
  button:active,
  button:visited,
  button:focus-visible,
  button:focus-within,
  button:target,
  button:-webkit-any-link,
  button:-moz-any-link {
    background: #ffd700 !important;
    background-color: #ffd700 !important;
  }
}

/* Nuclear option: force yellow on everything */
.card button,
.card button:hover,
.card button:focus,
.card button:active,
.card button:visited {
  background: #ffd700 !important;
  background-color: #ffd700 !important;
}

.result {
  font-size: 2rem;
  margin: 1rem 0;
}

.next {
  background: #6600ff;
  color: white;
  font-size: 1.2rem;
  border: none;
  padding: 0.7rem 1.5rem;
  border-radius: 0.5rem;
  cursor: pointer;
  margin-top: 1rem;
}
.next:hover { background: #9933ff; }

.loading-screen {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: black;
  flex-direction: column;
}
.fog {
  width: 100%;
  height: 100%;
  background: radial-gradient(circle, rgba(255,255,255,0.2) 0%, transparent 70%);
  position: absolute;
  animation: fog 4s infinite alternate;
}
@keyframes fog {
  from { opacity: 0.3; }
  to { opacity: 0.6; }
}
.loading-text {
  font-size: 2rem;
  color: orange;
  text-shadow: 0 0 10px red;
}
.fade-in { animation: fadeIn 1s ease; }
@keyframes fadeIn { from { opacity: 0; } to { opacity: 1; } }

/* Modal Styles */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(5px);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  animation: modalFadeIn 0.3s ease;
}

.modal-content {
  background: linear-gradient(145deg, #2b0033, #1a001f);
  border: 0.125rem solid #ff6b35;
  border-radius: 1.25rem;
  padding: 2rem;
  max-width: 90vw;
  max-height: 80vh;
  overflow-y: auto;
  box-shadow: 
    0 0 1.875rem rgba(255, 107, 53, 0.5),
    inset 0 0 1.25rem rgba(255, 255, 255, 0.1);
  text-align: center;
  animation: modalSlideIn 0.4s ease;
}

.modal-result {
  font-size: 3rem;
  margin-bottom: 1.5rem;
  text-shadow: 0 0 15px currentColor;
  animation: pulse 1s ease-in-out;
}

.revealed-stats {
  margin: 1.5rem 0;
}

.revealed-stats h3 {
  color: #ff6b35;
  margin-bottom: 1rem;
  font-size: 1.3rem;
}

.stat-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0.8rem 0;
  padding: 0.5rem;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 0.5rem;
}

.stat-label {
  font-weight: bold;
  flex: 1;
  text-align: left;
}

.stat-values {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.winning-stat {
  color: #4ade80;
  font-weight: bold;
  font-size: 1.2rem;
  text-shadow: 0 0 10px #4ade80;
}

.losing-stat {
  color: #ef4444;
  opacity: 0.7;
}

.vs {
  color: #ff6b35;
  font-weight: bold;
}

.modal-progress {
  margin-top: 2rem;
}

.progress-text {
  font-size: 1.2rem;
  margin-bottom: 1rem;
  color: #ff6b35;
}

.progress-bar-container {
  width: 100%;
  height: 0.75rem;
  background: #333;
  border-radius: 0.375rem;
  border: 0.063rem solid #666;
  overflow: hidden;
  margin: 1rem 0;
}

.countdown-text {
  font-size: 1rem;
  color: #ccc;
  margin-top: 0.5rem;
}

@keyframes modalFadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes modalSlideIn {
  from { transform: translateY(-3.125rem) scale(0.9); opacity: 0; }
  to { transform: translateY(0) scale(1); opacity: 1; }
}

@keyframes pulse {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.05); }
}

@keyframes subtleFloat {
  0%, 100% { transform: translateY(0) rotate(var(--base-rotation, 0deg)); }
  50% { transform: translateY(-0.25rem) rotate(var(--base-rotation, 0deg)); }
}

@keyframes gentleSway {
  0%, 100% { transform: rotate(-0.5deg); }
  50% { transform: rotate(0.5deg); }
}

@keyframes subtleWobble {
  0%, 100% { transform: rotate(0deg); }
  25% { transform: rotate(0.3deg); }
  75% { transform: rotate(-0.3deg); }
}

/* Mobile responsive modal */
@media (max-width: 48rem) {
  .modal-content {
    margin: 1rem;
    padding: 1.5rem;
    max-width: calc(100vw - 2rem);
  }
  
  .modal-result {
    font-size: 2.8rem;
  }
  
  .stat-row {
    flex-direction: column;
    gap: 0.5rem;
  }
  
  .stat-values {
    justify-content: center;
  }
}

/* Accessibility Skip Link */
.skip-link {
  position: absolute;
  top: -2.563rem;
  left: 0.375rem;
  background: linear-gradient(135deg, #2b0033, #1a001f);
  color: #ff6b35;
  padding: 0.5rem 1rem;
  text-decoration: none;
  border-radius: 0.5rem;
  border: 0.125rem solid #ff6b35;
  font-weight: bold;
  z-index: 1000;
  transition: all 0.3s ease;
  text-shadow: 0 0 10px #ff6b35;
  box-shadow: 0 0 20px rgba(255, 107, 53, 0.3);
}

.skip-link:focus {
  top: 0.375rem;
  animation: spookyGlow 2s ease-in-out infinite alternate;
}

@keyframes spookyGlow {
  from { 
    box-shadow: 0 0 1.25rem rgba(255, 107, 53, 0.3);
  }
  to { 
    box-shadow: 0 0 1.875rem rgba(255, 107, 53, 0.6);
  }
}

/* Score Display */
.score-display {
  font-size: 1.4rem;
  font-weight: bold;
  color: white;
  text-shadow: 0 0 15px rgba(255, 255, 255, 0.8);
  margin: 1rem 0;
  text-align: center;
}

/* Spooky Instructions */
.instructions {
  max-width: 37.5rem;
  margin: 1.5rem auto;
  padding: 1.5rem;
  background: linear-gradient(135deg, rgba(43, 0, 51, 0.8), rgba(26, 0, 31, 0.9));
  border: 0.125rem solid #ff6b35;
  border-radius: 0.938rem;
  box-shadow: 
    0 0 1.25rem rgba(255, 107, 53, 0.3),
    inset 0 0 0.938rem rgba(255, 255, 255, 0.05);
  animation: spookyGlow 3s ease-in-out infinite alternate;
}

.spooky-instruction {
  font-size: 1.8rem;
  color: #ff6b35;
  margin-bottom: 1rem;
  text-shadow: 0 0 10px #ff6b35;
  animation: flicker 2s ease-in-out infinite alternate;
}

.instruction-text {
  font-size: 1.3rem;
  line-height: 1.6;
  color: #e0e0e0;
  margin-bottom: 1rem;
  text-shadow: 0 0 5px rgba(255, 255, 255, 0.3);
}

.highlight {
  color: #4ade80;
  font-weight: bold;
  text-shadow: 0 0 1rem #4ade80;
}

.warning-text {
  font-size: 1rem;
  color: #ef4444;
  text-shadow: 0 0 0.5rem #ef4444;
  margin-bottom: 0;
  animation: pulse 2s ease-in-out infinite;
}

/* Recognition Challenge Styling */
.recognition-challenge {
  margin-top: 1.5rem;
  padding: 1.25rem;
  background: linear-gradient(135deg, rgba(75, 0, 130, 0.3), rgba(25, 0, 51, 0.5));
  border: 0.125rem dashed #9d4edd;
  border-radius: 0.75rem;
  animation: mysteryShadow 4s ease-in-out infinite alternate;
}

.mystery-text {
  font-size: 1.1rem;
  color: #c77dff;
  margin-bottom: 0.75rem;
  text-shadow: 0 0 0.625rem rgba(199, 125, 255, 0.5);
  line-height: 1.5;
}

.hint-text {
  font-size: 0.95rem;
  color: #e0aaff;
  margin-bottom: 0;
  font-style: italic;
  text-shadow: 0 0 0.5rem rgba(224, 170, 255, 0.4);
  opacity: 0.9;
}

@keyframes mysteryShadow {
  0% { 
    box-shadow: 0 0 0.938rem rgba(157, 78, 221, 0.3);
    border-color: #9d4edd;
  }
  100% { 
    box-shadow: 0 0 1.25rem rgba(199, 125, 255, 0.5);
    border-color: #c77dff;
  }
}

@keyframes spookyGlow {
  0% { box-shadow: 0 0 1.25rem rgba(255, 107, 53, 0.3), inset 0 0 0.938rem rgba(255, 255, 255, 0.05); }
  100% { box-shadow: 0 0 1.875rem rgba(255, 107, 53, 0.6), inset 0 0 1.25rem rgba(255, 255, 255, 0.1); }
}

@keyframes flicker {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.8; }
}

/* Hide instructions on smaller mobile screens to save space */
@media (max-width: 30rem) {
  .instructions {
    margin: 1rem;
    padding: 1rem;
  }
  
  .spooky-instruction {
    font-size: 1.2rem;
  }
  
  .instruction-text {
    font-size: 1rem;
  }
  
  .warning-text {
    font-size: 0.9rem;
  }
  
  .recognition-challenge {
    margin-top: 1rem;
    padding: 1rem;
  }
  
  .mystery-text {
    font-size: 1rem;
  }
  
  .hint-text {
    font-size: 0.85rem;
  }
}
